.title {
  margin: 0;
  font-weight: 700;
  font-size: 55px;
  line-height: 67px;
  text-align: center;
  color: var(--black);
}

@media (max-width: 1024px) {
  .title {
    font-size: 45px;
  }
}
@media (max-width: 576px) {
  .title {
    font-size: 37px;
    line-height: 49px;
  }
}
.button {
  width: 200px;
  height: 60px;
  border: none;
  border-radius: 30px;
  background: var(--gradient-pink);
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: var(--white);
  cursor: pointer;
}

.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 2;
  padding: 65px 100px;
}

.header__layout {
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
}

.menu--active {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 10;
  background: var(--white);
  height: 100%;
}

.menu--active .header__layout {
  display: grid;
  grid-template-areas: "logo burger" "menu menu" "social social";
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: min-content min-content min-content;
  row-gap: 40px;
  width: 100%;
  height: 100%;
}

.header__logo {
  position: relative;
  z-index: 10;
  flex-shrink: 0;
  grid-area: logo;
  border-radius: 18px;
  margin: 0;
  margin-right: 60px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
  color: var(--pink);
}

.header__menu-nav-tablet {
  display: none;
}

.header__menu-nav {
  grid-area: menu;
  width: 100%;
  max-width: 840px;
}

.menu--active .header__menu-nav {
  display: block;
  margin: 0;
}

.header__menu-close {
  display: none;
}

.header__menu-list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: var(--black);
}

.menu--active .header__menu-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.header__menu-item:not(:last-of-type) {
  margin-right: 50px;
}

.header__social {
  display: flex;
  grid-area: social;
