.reviews {
  position: relative;
  padding-top: 185px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &-pink {
    position: absolute;
    z-index: 0;
    top: -15px;
    right: -100px;
    width: 447px;
    height: 444px;
    background: var(--pink);
    filter: blur(200px);
  }

  &-slider {
    position: relative;
    margin-top: 50px;
    height: 620px;
    width: 1225px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      cursor: pointer;
    }
  }

  .slick-track {
    display: flex;
  }

  .slick-list {
    overflow: hidden;
  }

  .slick-slide {
    margin: 0 10px 0 0;
    width: 300px !important;
    height: 550px !important;

    div {
      height: 100%;
      width: 100%;
      div {
        height: 100%;
        width: 100%;
      }
    }
  }

  .slick-prev,
  .slick-next {
    position: absolute;
    z-index: 1;
    background: rgba(0, 0, 0, 0);
    border: none;
    outline: none;
    bottom: 0;
    width: 60px;
    height: 60px;
    cursor: pointer;
  }
  .slick-prev {
    left: -25px;
  }
  .slick-next {
    right: 0;
  }
}

@media (max-width: 1400px) {
  .reviews {
    &-slider {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      width: 500px;
    }

    .slick-list {
      width: 500px;
    }

    .slick-slide {
      margin: 0 20px 0 10px;
      width: 500px !important;
      height: 550px !important;
    }

    .slick-prev {
      left: -15px;
    }
    .slick-next {
      right: -5px;
    }
  }
}

@include tablet {
  .reviews {
    padding-top: 140px;

    &-pink {
      display: none;
    }
  }
}

@include mobile {
  .reviews {
    &-slider {
      margin-top: 30px;
      width: 300px;
    }

    .slick-list {
      width: 300px;
    }

    .slick-slide {
      margin: 0 20px 0 10px;
      width: 300px !important;
      height: 550px !important;
    }

    .slick-prev,
    .slick-next {
      width: 45px;
      height: 45px;
    }
  }
}
