.footer {
  height: 210px;
  background: var(--gradient-white);

  &-container {
    margin: 0;
    padding: 0 100px;
    max-width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-text {
    display: flex;
    align-items: center;
  }

  &-title {
    margin-right: 50px;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
    color: var(--background-white);
  }

  &-link {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: var(--background-white);
    @include mr(50px);

    &:hover {
      color: var(--background-white);
    }
  }

  &-icon {
    @include mr(25px);

    img {
      width: 40px;
      height: 40px;

      transform: scale(1);
      transition: transform 0.3s;

      &:hover {
        transform: scale(1.1);
      }
    }
  }
}

@media (max-width: 1200px) {
  .footer {
    &-navigation {
      display: none;
    }
  }
}

@include small-tablet {
  .footer {
    height: 205px;

    &-container {
      padding: 50px 15px;
      flex-direction: column;
      justify-content: center;
    }

    &-title {
      margin-right: 0;
    }
  }
}
